<template>
  <div class="chat-main">
    <!--  -->
    <div class="chat-head">
      <!-- <div class="left" @click="$router.back()"><van-icon class="arrow-left" name="arrow-left" /></div> -->
      <div></div>
      <h1>{{friend.userNick}}</h1>
      <div class="right"></div>
    </div>
    <!--  -->
    <div class="chat-top" :class="{show:active}">
      <div class="chat-more" @click="getChatMore">
        -------{{chatMore ? '' : ''}}-------
      </div>
      <!--  -->
      <div class="chat-list" v-for="(item,index) in list" :key="index">
        <div class="chat-img">
          <img class="left" v-if="item.id!==id && item.time===''" :src="getImg(item.img)" alt="">
        </div>
        <div class="chat-content-main">
          <div v-if="item.content" class="chat-name" :class="{'is-right':item.id===id}">{{item.name}}</div>
          <div v-if="item.content" class="chat-content-model" :class="{'is-right': item.id===id}">
            <div class="chat-content" :class="{'active': item.id!==id}">{{item.content}}</div>
          </div>
          <div class="chat-time" v-if="!item.content">{{item.time}}</div>
        </div>
        <div class="chat-img">
          <img class="right" v-if="item.id===id" :src="getImg(item.img)" alt="">
        </div>
      </div>
    </div>
    <!--  -->
    <div class="chat-foot">
      <div class="chat-foot-top">
        <div class="chat-emo" @click="getEmoView"></div>
        <!--  -->
        <input class="chat-input" v-model="context" id="context" @focus="getFocus"/>
        <div class="chat-send">
          <div>
            <label v-show="!active" @click="toSendMsg" for="context"></label>
            <label v-show="active" @click="toSendMsg"></label>
          </div>
        </div>
      </div>
      <!-- <img :src="getImgs('xixiao.png')"> -->
      <div class="chat-bot" :class="{show:active}">
        <div class="chat-model">
          <div class="swiper mySwiper1"  >
            <div class="swiper-wrapper">
              <div class="swiper-slide" v-for="(item, index) in emoList" :key="index">
                <div class="chat-row">
                  <div 
                    v-for="(item,index) in item"
                    :key="index"
                    @click="getEmo(item)"
                  >{{item}}</div>
                </div>
              </div>
            </div>
            <div class="swiper-pagination"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Swiper from "swiper/swiper-bundle.min.js";
import "swiper/swiper-bundle.min.css";
import {listMath} from './js'
import { Toast } from 'vant';
import { getPageMessageList } from '@/axios/index' 

export default {
  data() {
    return {
      // id: '1',
      // name: '',
      startText: {
        id: '2',
        name: '',
        img: '1.jpg',
        content: '',
        time:'',
      },
      // {
      //   "fromUser":"1",
      //   "fromUserNickname":"1",
      //   "toUser":"2",
      //   "toUserNickname":"2",
      //   "sendMessage":""
      // }
      list: [
        // {
        //   id: '002',
        //   name: '',
        //   img: '2.jpg',
        //   content: 'heiheiehiheiheiehiheiheiehiheiheiehiheiheiehiheiheiehi',
        //   time: '',
        // },
      ],
      emojis: ['','','','','','','','','',
      '','','','','','','','','','','','','','','',
      '','','','','','','','','','','','','','','','','','',
      '','','','','','','','','',
      '','','','','','','','',
      '','','','','','','','','','','','','','','',
      '','','','','',
      '','','','','','','','',
      '','','','','','','','','','','',
      '','','','','','','','','','',
      '','','','','','','','','','','','',
      '','','','','','','','','','','','','','','','','',
      '','','','','','','','','','','','','','',
      '','','','','','','',
      '','','','','','','','','','','','','','','','',
      '','','','','','','','','','','','','','',
      '','','','','','','','',
      ],
      context: '',
      active: false,
      emoList: [],
      websock: null,
      users:{
        // account: "wangdongdong"
        // id: 1
        // password: "4c7a0e30e2874f2534e8892a5ddde722"
        // userName: "dd"
        // userNick: ""
      }, // 
      friend:{
        // account: "wangdongdong"
        // id: 2
        // password: "4c7a0e30e2874f2534e8892a5ddde722"
        // userName: "dd"
        // userNick: ""
      }, // 
      chatText:{}, // 
      userList:[], // 
      pageNumber: 0, // 
      chatMore: true, // 
    }
  },
  computed:{
    name() {
      return this.users.userNick || ''
    },
    id(){
      return this.users.id || ''
    }
  },
  mounted() {
    this.getInit();
  },
  created() {
    this.initWebSocket(); // ws
    this.getInitUser(); // 
    this.getChatMore(); // 
  },
  methods:{
    // 
    async getChatMore() {
      if(!this.chatMore) {
        return;
      }
      let data={
        pageNumber: this.pageNumber
      }
      try{
        let res = await getPageMessageList(data);
        this.pageNumber++;
        console.log('====res==',res)
        if(res.result.content.length){
          this.getList(res.result.content)
          this.chatMore = true
        } else {
          this.chatMore = false
        }
      } catch(e){
        console.log(e)
      }
    },
    // 
    getInitUser(){
      let userInfo = JSON.parse(localStorage.getItem('userid'));
      
      if(!userInfo){
        this.$router.push({path: '/login'})
      }

      this.users = userInfo || {};
      console.log('u===========',this.users)
      // 
      let list = [
        {
          account: "wangdongdong",
          id: 1,
          password: "4c7a0e30e2874f2534e8892a5ddde722",
          userName: "dd",
          userNick: "",
        },
        {
          account: "ymh",
          id: 2,
          password: "13710f6ab4560927385977dc93b0d95a",
          userName: "y",
          userNick: "666",
        }
      ]
      this.userList = list;
      this.friend = userInfo.id === 1 ? list[1] : list[0];
      console.log('this=====',this)
    },
    // 
    getList(res){
      // console.log('res=',res)
      let list = res
      list = list.map(item=>{
        return {
          ...item,
          id: item.fromUser ,
          name: item.fromUserNickname,
          img: item.fromUser === 1 ? '2.jpg' : '1.jpg',
          content: item.sendMessage,
          time: '',
        }
      })
      list.forEach(item => {
        this.list.push(item);
      })
      console.log(this.pageNumber)
      if(this.pageNumber <= 1){
        this.setScroll();
      }
    },
    // 
    getCaht() {
      setTimeout(()=>{
        this.list.push(this.startText);
      },500)
    },
    // 
    getInit(){
      // 
      let list = []
      this.emojis.forEach((item, index) => {
        list.push(item);
        if(list.length === 28){
          this.emoList.push(list);
          list = [];
        }
        // 
        let a = this.emojis.length/28 !== Math.ceil(this.emojis.length/28);
        // 
        let b = Math.ceil(this.emojis.length/28) === this.emoList.length+1;
        // 
        let c = this.emojis.length-1 === index;
        if( a && b && c) {
          this.emoList.push(list);
        }
      });
      let swiper = new Swiper(".mySwiper1", {
        spaceBetween: 10,
        pagination: {
          el: ".swiper-pagination",
        },
      });
    },
    // 
    toSend() {
      this.context.trim();
      if(!this.context.trim()){
        return;
      }
      let obj = {
        id: this.id,
        name: this.name,
        img: '2.jpg',
        content: this.context,
        time: '',
      };
      // 
      let obj1 = {
        id: '2',
        name: '',
        img: '1.jpg',
        content: this.getContext(),
        time: '',
      };
      this.list.push(obj);
      // 
      setTimeout(()=>{
        this.list.push(obj1);
        this.setScroll();
      },500)
      this.context = '';
      this.setScroll();
      // this.toSendMsg();
    },
    // 
    toSendMsg() {
      console.log('con===',this.context.trim())
      if(!this.context.trim()){
        return;
      }
      
      // {
      //   "fromUser":"1",
      //   "fromUserNickname":"1",
      //   "toUser":"2",
      //   "toUserNickname":"2",
      //   "sendMessage":""
      // }
      this.websocketsend(JSON.stringify({
        // from:'1', to:'2', content:this.context.trim()
        fromUser:this.id,
        fromUserNickname:this.name,
        toUser: this.id === 1 ? this.userList[1].id : this.userList[0].id,
        toUserNickname: this.id === 1 ? this.userList[1].userNick : this.userList[0].userNick,
        sendMessage: this.context.trim()
      }));
      this.context=''
      // this.$socket.emit('sendMsg', {from:'001', to:'002', content:this.context.trim()})
    },
    // 
    getContext() {
      try {
        let index = Math.ceil(Math.random()*10);
        // console.log(index);
        let context = listMath(index);
        return context;
      } catch(e) {
        console.log(e);
        return '';
      }
    },
    // 
    getFocus() {
      this.active = false;
    },
    // 
    setScroll() {
      this.$nextTick(()=>{
        window.scrollTo(0, document.body.scrollHeight)
      })
    },
    // 
    getEmo(item) {
      this.context = this.context + item;
    },
    // 
    getEmoView() {
      this.active = !this.active;
      this.setScroll();
    },
    // 
    getImg(data) {
      if (data) {
        let img = require(`../../assets/images/chat/${data}`);
        return img;
      }
    },


    initWebSocket(){ //weosocket
      const wsuri = "ws://116.204.116.99/:8887/";
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    websocketonopen(){ //send
      console.log('')
      // let actions = {"test":"12345"};
      // this.websocketsend(JSON.stringify(actions));
    },
    websocketonerror(){//
      console.log('')
      Toast.fail('');
      this.initWebSocket();
    },
    websocketonmessage(e){ //
      try {
        console.log('==',e.data);
        console.log('==',typeof e.data);
        console.log('fromUser==', e.data.fromUser);
      
        const redata = JSON.parse(e.data);
        console.log('---',redata.fromUser);

        if (redata.fromUser){
          this.getChatInfo(redata);
        }
      } catch(e) {
        console.log(e)
      }
    },
    websocketsend(Data){//
      console.log('',Data)
      this.websock.send(Data);
    },
    websocketclose(e){  //
      console.log('',e);
    },

    // 
    getChatInfo(data) {
      console.log('123===',data)
      console.log('123===',data.sendMessage)
      let obj = {
        id: data.fromUser ,
        name: data.fromUserNickname,
        img: data.fromUser === 1 ? '2.jpg' : '1.jpg',
        content: data.sendMessage,
        time: '',
      };
     
      this.list.push(obj);
      
      this.setScroll();
    },

    //
    getEmojis() {
      let list = [
        {
          key: '[]',
          value: 'weixiao.png'
        },
        {
          key: '[]',
          value: 'xixiao.png'
        }
      ]
    },
    // 
    getImgs(data) {
      if (data) {
        let img = require(`../../assets/images/chat/${data}`);
        return img;
      }
    },
  },

  destroyed() {
    this.websock.close() //websocket
  },

}
</script>

<style lang="less" scoped>
  .chat-main{
    .chat-head {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 49px;
      line-height: 49px;
      border-bottom: 1px solid #eee;
      box-sizing: content-box;
      background: #fff;
      position: fixed;
      top: 0;
      padding: 0 12px 0 4px;
      box-sizing: border-box;
      z-index: 9;
      .left{
        .arrow-left{
          margin-top: 17px;
        }
        cursor: pointer;
      }
      h1 {
        font-size: 18px;
      }
      .right{
      }
    }
    .chat-top{
      padding: 50px 0 50px 0;
      box-sizing: border-box;
      &.show{
        padding: 50px 0 250px 0;
        box-sizing: border-box;
      }
      .chat-more{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 4px;
        font-size: 14px;
        cursor: pointer;
        color: #9f9f9f;
      }
    }
    .chat-list{
      display: flex;
      padding: 12px 0;
      box-sizing: border-box;
      animation: bg .5s;
      .chat-img{
        width: 50px;
        min-width: 50px;
        height: 50px;
        img {
          width: 40px;
          height: 40px;
          object-fit: cover;
          border-radius: 50%;
          box-shadow: 0px 0px 2px 0px #cdcdcd;
          &.left{
            float: right;
          }
          &.right{
            float: left;
          }
          &:after {
            clear: both;
            content: "";
            display: block;
          }
        }
      }
      .chat-content-main{
        flex:1;
        padding: 0 6px;
        box-sizing: border-box;
        .chat-name{
          font-size: 12px;
          padding: 4px 0;
          box-sizing: border-box;
          &.is-right{
            text-align: right;
          }
        }
        .chat-content-model{
          display: flex;
          &.is-right{
            justify-content: flex-end;
          }
          .chat-content{
            font-size: 16px;
            background: #628bff;
            padding: 12px;
            border-radius: 4px;
            word-break: break-word;
            box-shadow: 1px 1px 2px 1px #f4f4f4;
            line-height: 22px;
            letter-spacing: 1px;
            &.active{
              background: #fff;
            }
          }
        }
        .chat-time{
          text-align: center;
          font-size: 16px;
          line-height: 50px;
          color: #505050;
        }
      }
    }
    .chat-foot{
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      background: #fafafa;
      z-index: 1;
      .chat-foot-top{
        width: 100%;
        height: 50px;
        display: flex;
        background: #fff;
        .chat-emo{
          min-width:50px;
          width: 50px;
          height: 50px;
          text-align: center;
          line-height: 50px;
          cursor: pointer;
        }
        .chat-input{
          flex: 1;
          height: 34px;
          font-size: 16px;
          padding: 0 12px 0;
          box-sizing: border-box;
          margin-top: 8px;
          background: #f9f9f9;
          border-radius: 4px;
        }
        .chat-send{
          min-width:70px;
          width: 70px;
          height: 50px;
          text-align: center;
          line-height: 50px;
          font-size: 15px;
          >div{
            background: #628bff;
            width: 50px;
            height: 30px;
            line-height: 30px;
            border-radius: 4px;
            margin-top: 10px;
            margin-left: 10px;
            color: #fff;
            cursor: pointer;
          }
        }
      }
      .chat-bot{
        height:200px;
        background: #fafafa;
        display: none;
        padding: 10px 12px 0 12px;
        box-sizing: border-box;
        &.show{
          display: block;
        }
        .chat-model{
          .chat-row{
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            >div{
              width: 14%;
              height: 40px;
              line-height: 40px;
              text-align: center;
              cursor: pointer;
            }
          }
          .swiper{
            height: 188px;
          }
        }
      }
    }
  }
  @keyframes bg {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
</style>>
